/* ========== 기본: Light ========== */

.broadcastlive-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    padding: 16px 24px;
    border-bottom: 1px solid #eaeaea;
    color: #222;
    margin-bottom: 0;
}

.header-left {
    display: flex;
    align-items: baseline;
    gap: 8px;
}

.broadcast-title {
    font-size: 20px;
    font-weight: 700;
    margin: 0;
    color: #222;
}

.header-right {
    display: flex;
    align-items: center;
    gap: 10px;
}

/* 포커스 모드 버튼 */
.focus-button {
    background-color: #f2f2f4;
    color: #333;
    border: 1px solid #e4e4ea;
    padding: 6px 12px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color .15s ease, border-color .15s ease, filter .15s ease;
}
.focus-button:hover { background-color: #e9e9ef; }
.focus-button.active {
    background-color: #6f42c1;
    color: #fff;
    border-color: #6f42c1;
}

.exit-button {
    background-color: #f2f2f4;
    color: red;
    border: 1px solid #e4e4ea;
    padding: 6px 12px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.2s;
}
.exit-button:hover { background-color: #e9e9ef; }

/* ========== Dark Mode Overrides ========== */

.dark-mode .broadcastlive-header {
    background-color: #1a1a1a;
    border-bottom: 1px solid #333;
    color: #fff;
}

.dark-mode .broadcast-title { color: #fff; }

.dark-mode .focus-button {
    background-color: #2b2b34;
    color: #eaeaf2;
    border: 1px solid #3a3a48;
}
.dark-mode .focus-button:hover { background-color: #34343f; }
.dark-mode .focus-button.active {
    background-color: #6a0dad;
    border-color: #6a0dad;
    color: #fff;
}

.dark-mode .exit-button {
    background-color: #333;
    color: red;
    border: none;
}
.dark-mode .exit-button:hover { background-color: #4a4a4a; }
